import React, {Component} from 'react';
import Icon from "../component/Icon";

class PageIcon extends Component {
    render() {
        return (
            <div>
                <div className={"titleColor fSize"+24}>Icon  图标</div>
                <div className="fSize14 bodyColor">
                    <p>语义化的矢量图形。</p>
                </div>
                <section>
                    <ul className="anticons-list">
                        <li className="">
                            <Icon title="icon-save"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-save</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-down"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-down</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-export"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-export</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-check"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-check</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-close"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-close</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-user"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-user</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-solution"/>

                            <span className="anticon-class">
                                <span className="ant-badge">icon-solution</span>
                            </span>
                        </li>

                        <li className="">
                            <Icon title="icon-credit-card"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-credit-card</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-folder-open"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-folder-open</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-home"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-home</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-file-text"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-file-text</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-upload"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-upload</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-paper-clip"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-paper-clip</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-menu-fold"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-menu-fold</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-search"/>
                            <span className="anticon-class">
                                <span className="ant-badge">icon-search</span>
                            </span>
                        </li>
                        <li className="">
                            <Icon title="icon-bell"/>
                            <span className="anticon-class">
                                    <span className="ant-badge">icon-bell</span>
                                </span>
                        </li>

                    </ul>
                </section>
            </div>
        );
    }
}

export default PageIcon;