import React, {Component} from 'react';
import Checkbox from "./Checkbox";

class Tbody extends Component {
    constructor(props) {
        super(props);
        this.state={
            dataSource:"",
            defaultValue:"",
            allValue:"",
            indeterminate:"",
        }
    }
    componentWillMount() {
        if(this.props.dataSource){
            this.setState({
                dataSource:this.props.dataSource,
            })
        }
    }

    onChange=(e)=>{
        let newDataSource=[];

        console.log(e.currentTarget.checked);
        let key=e.currentTarget.getAttribute("data-row-key")
        let dataSource=this.state.dataSource;
        for(var i=0;i<dataSource.length;i++) {
            if (dataSource[i].key === key) {
                newDataSource.push(dataSource[i]);
            }
        }

        this.props.onRow(newDataSource[0])
        if(e.currentTarget.checked){
           //
        }

    }
    render() {
        const {dataSource,defaultValue,allValue,indeterminate}=this.props
      //  console.log(dataSource)
        return (
            <tbody className="xg-table-tbody">
            {
                dataSource.map((v,k)=>{
                    return(
                        <tr key={k} className="xg-table-row xg-table-row-level-0" data-row-key={v.key}>
                            {
                                this.props.rowSelection !=undefined?(
                                    <td className="xg-table-selection-column">
                                        <span>
                                            <Checkbox
                                                onChange={this.onChange}
                                                name={v.name}
                                                rowkey={v.key}
                                                value={v.key}

                                                key={k}
                                                defaultValue={defaultValue}
                                                allValue={allValue}

                                                disabled={this.props.disabled}

                                                indeterminate={indeterminate}
                                            ></Checkbox>
                                        </span>
                                    </td>):""
                            }
                            <td className=""><a>{v.name}</a></td>
                            <td className="">{v.age}</td>
                            <td className="">{v.address}</td>
                            <td className=""><span><span
                                className="xg-tag xg-tag-green">NICE</span><span
                                className="xg-tag xg-tag-geekblue">DEVELOPER</span></span>
                            </td>
                            <td className=""><span><a>Invite John Brown</a><div
                                className="xg-divider xg-divider-vertical"
                                role="separator"></div><a>Delete</a></span></td>
                        </tr>
                        )

                })
            }

            </tbody>

        );
    }
}

export default Tbody;