import React, {Component} from 'react';
import Checkbox from "./Checkbox";

class Thead extends Component {
    constructor(props){
        super(props);
        this.state={
            /*全选*/
            checkedList: "",
            indeterminate: true,
            checkAll: false,
        }
    }
    componentWillMount() {
        // this.props.onRow
    }
    onChange=(e)=>{
        console.log(e)
    }
    render() {
        console.log(this.props)
        const {columns,indeterminate}=this.props
        return (
            <thead className="xg-table-thead">

            <tr>
                {
                    this.props.rowSelection !=undefined?(<th className="xg-table-selection-column">
                                                            <span className="xg-table-header-column">
                                                                <div>
                                                                    <span className="xg-table-column-title">
                                                                        <div className="xg-table-selection">
                                                                           <Checkbox onChange={this.onChange} indeterminate={indeterminate}  onChange={this.onCheckAllChange} checked={this.state.checkAll}></Checkbox>

                                                                        </div>
                                                                    </span>
                                                                    <span className="xg-table-column-sorter"></span>
                                                                </div>
                                                            </span>
                    </th>):""
                }
                {
                    columns.map((v,k)=>{
                        return(
                            <th className="" key={k}><span className="xg-table-header-column"><div>
                                <span className="xg-table-column-title">{v.title}</span>
                                <span className="xg-table-column-sorter"></span></div></span>
                            </th>
                        )
                    })
                }


                <th className=""><span className="xg-table-header-column"><div><span
                    className="xg-table-column-title">Tags</span><span
                    className="xg-table-column-sorter"></span></div></span>
                </th>
                <th className=""><span className="xg-table-header-column"><div><span
                    className="xg-table-column-title">Action</span><span
                    className="xg-table-column-sorter"></span></div></span>
                </th>
            </tr>
            </thead>
        );
    }
}

export default Thead;